import store from '../vuex'
import router from '../router'
import fetch from './fetch'

export const 
check = (fileName, data) => router.push({
  name: fileName + '_check',
  params: {  data }
}),
 edit = (fileName, data) => router.push({
  name: fileName + '_edit',
  params: { data }
}),
 add = (fileName, data) => router.push({
  name: fileName + '_add',
  params: { data }
}),
 push = fileName => {
  router.push(fileName)
},
remove = function (ids, list, url) {
  this.$confirm('此操作将永久删除该条信息, 是否继续?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
    .then(() => {
      // fetch() 对后台发起删除请求
      for (let i in list) {
        for (let j in ids)
        if (ids[j] == list[i].id)
        store.commit('REMOVE', list, i) 
      }
      this.$message({
        type: 'success',
        message: '删除成功!'
      })
    })
    .catch(() => {
      this.$message({
        type: 'info',
        message: '已取消删除'
      })
    })
}




